* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  display: flex;

  justify-content: space-between;
  flex: 1;
  padding: 12px;
  position: relative;
  align-items: center;
}
.menu {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}
.mob-menu {
  display: none;
}
.web-menu {
  display: flex;
}
.logo {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  /* font-size: 36px;
  color: blueviolet;
  font-weight: 800; */
}
.logo img {
  width: 25%;
}

@media only screen and (max-width: 720px) {
  .mob-menu {
    display: flex;
  }
  .web-menu {
    display: none;
  }
  .logo {
    font-size: 24px;
  }
}
